<template>
  <div class="loader" v-if="!orderInfo">
    <img src="/img/Iphone-spinner-2.gif" alt="Загрузка...">
  </div>
  <main class="content container" v-else>
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'cart'}">
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Заказ оформлен <span>№ {{ orderInfo.id }}</span>
      </h1>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <p class="cart__message">
            Благодарим за&nbsp;выбор нашего магазина. На&nbsp;Вашу почту придет письмо
            с&nbsp;деталями заказа.
            Наши менеджеры свяжутся с&nbsp;Вами в&nbsp;течение часа для уточнения деталей доставки.
          </p>

          <ul class="dictionary">
            <li class="dictionary__item">
              <span class="dictionary__key">
                Получатель
              </span>
              <span class="dictionary__value">
                {{ orderInfo.name }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Адрес доставки
              </span>
              <span class="dictionary__value">
                {{ orderInfo.address }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Телефон
              </span>
              <span class="dictionary__value">
                {{ orderInfo.phone }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Email
              </span>
              <span class="dictionary__value">
                {{ orderInfo.email }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Способ оплаты
              </span>
              <span class="dictionary__value">
                картой при получении
              </span>
            </li>
          </ul>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <order-cart-item v-for="item in orderInfo.basket.items" :item="item" :key="item.id"/>
          </ul>

          <div class="cart__total">
            <p>Доставка: <b>500 ₽</b></p>
            <p>Итого: <b>{{ productsInOrder }}</b> товара
              на сумму <b>{{ orderTotalPrice | numberFormat }} ₽</b></p>
          </div>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import { mapGetters } from 'vuex';
import orderCartItem from '@/components/OrderCartItem.vue';
import numberFormat from '@/helpers/numberFormat';

export default {
  name: 'OrderInfoPage',
  components: { orderCartItem },
  computed: {
    ...mapGetters(['orderInfo']),
    productsInOrder() {
      return this.orderInfo.basket.items.length;
    },
    orderTotalPrice() {
      return this.orderInfo.basket.items
        .reduce((acc, item) => (item.price * item.quantity) + acc, 0);
    },
  },
  created() {
    if (this.$store.state.orderInfo
      && this.store.state.orderInfo.id === this.$route.params.orderId
    ) {
      return;
    }

    this.$store.dispatch('loadOrderInfo', this.$route.params.orderId);
  },
  filters: { numberFormat },
};
</script>

<style scoped>
.loader {
  text-align: center;
}
</style>
