import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';
import { CONFIG } from '@/config';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    cartProducts: [],
    userAccessKey: null,
    cartProductsData: null,
    loadingCartData: false,
    loadingCartDataError: false,
    orderInfo: null,
  },
  mutations: {
    updateCartProductAmount(state, { productId, amount }) {
      const item = state.cartProducts.find(
        (cartItem) => cartItem.productId === productId,
      );

      if (item) {
        item.amount = amount;
      }
    },
    updateUserAccessKey(state, accessKey) {
      state.userAccessKey = accessKey;
    },
    updateCartProductsData(state, items) {
      state.cartProductsData = items;
    },
    changeLoading(state, value) {
      state.loadingCartData = value;
    },
    startLoadingCartData(state) {
      state.loadingCartData = true;
      state.loadingCartDataError = false;
    },
    loadingCartDataSuccess(state) {
      state.loadingCartData = false;
      state.loadingCartDataError = false;
    },
    loadingCartDataFailed(state) {
      state.loadingCartData = false;
      state.loadingCartDataError = true;
    },
    resetCart(state) {
      state.cartProducts = [];
      state.cartProductsData = [];
    },
    updateOrderInfo(state, orderInfo) {
      state.orderInfo = orderInfo;
    },
  },
  getters: {
    cartDetailProducts(state) {
      return state.cartProductsData;
    },
    cartTotalPrice(state, getters) {
      return getters.cartDetailProducts ? getters.cartDetailProducts
        .reduce((acc, item) => (item.price * item.quantity) + acc, 0) : 0;
    },
    isLoadingCartData(state) {
      return state.loadingCartData;
    },
    isLoadingCartDataError(state) {
      return state.loadingCartDataError;
    },
    orderInfo(state) {
      return state.orderInfo;
    },
  },
  actions: {
    loadCart(context) {
      const url = (CONFIG.api.base_url) + (CONFIG.api.endpoints.cartData);
      const params = {};
      if (context.state.userAccessKey) {
        params.userAccessKey = context.state.userAccessKey;
      } else if (localStorage.getItem('userAccessKey')) {
        params.userAccessKey = localStorage.getItem('userAccessKey');
      }

      context.commit('startLoadingCartData');

      return axios.get(url, { params })
        .then((response) => {
          if (!localStorage.getItem('userAccessKey')) {
            localStorage.setItem('userAccessKey', response.data.user.accessKey);
          }

          if (!context.state.userAccessKey
            || context.stat.userAccessKey !== response.data.user.accessKey) {
            context.commit('updateUserAccessKey', response.data.user.accessKey);
          }
          context.commit('updateCartProductsData', response.data.items);
          context.commit('loadingCartDataSuccess');
        })
        .catch(() => context.commit('loadingCartDataFailed'));
    },
    addProductToCard(context, { productId, amount }) {
      const url = (CONFIG.api.base_url) + (CONFIG.api.endpoints.addToCart);
      return axios.post(url, {
        productId,
        quantity: amount,
      }, {
        params: {
          userAccessKey: context.state.userAccessKey,
        },
      }).then((response) => {
        context.commit('updateCartProductsData', response.data.items);
        context.commit('loadingCartDataSuccess');
      });
    },
    updateCartProductAmount(context, { productId, amount }) {
      context.commit('updateCartProductAmount', { productId, amount });

      const url = (CONFIG.api.base_url) + (CONFIG.api.endpoints.updateCartProduct);
      return axios.put(url, {
        productId,
        quantity: amount,
      }, {
        params: {
          userAccessKey: context.state.userAccessKey,
        },
      }).then((response) => {
        context.commit('updateCartProductsData', response.data.items);
      }).catch(() => {
        context.commit('loadingCartDataSuccess');
      });
    },
    removeCartProduct(context, productId) {
      const url = (CONFIG.api.base_url) + (CONFIG.api.endpoints.removeCartProduct);

      return axios.delete(url, {
        data: { productId },
        params: {
          userAccessKey: context.state.userAccessKey,
        },
      }).then((response) => {
        context.commit('updateCartProductsData', response.data.items);
        context.commit('loadingCartDataSuccess');
      });
    },
    loadOrderInfo(context, orderId) {
      const url = (CONFIG.api.base_url)
        + (CONFIG.api.endpoints.orderInfo).replace(':id', orderId);

      const params = {};
      if (context.state.userAccessKey) {
        params.userAccessKey = context.state.userAccessKey;
      } else if (localStorage.getItem('userAccessKey')) {
        params.userAccessKey = localStorage.getItem('userAccessKey');
      }

      console.log(context.state.userAccessKey);
      return axios.get(url, { params })
        .then((response) => {
          context.commit('updateOrderInfo', response.data);
        });
    },
  },
});
