<template>
  <div class="loader" v-if="isLoading">
    <img src="/img/Iphone-spinner-2.gif" alt="Загрузка...">
  </div>
  <router-link class="header__cart" :to="{name: 'cart'}" aria-label="Корзина с товарами" v-else>
    <svg width="30" height="21" fill="currentColor">
      <use xlink:href="#icon-cart"></use>
    </svg>
    <span class="header__count" aria-label="Количество товаров">
      {{ productsInCart }}
    </span>
  </router-link>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: 'CartIndicator',
  methods: {
    ...mapGetters(['cartDetailProducts', 'isLoadingCartData']),
  },
  computed: {
    productsInCart() {
      return this.cartDetailProducts().length;
    },
    isLoading() {
      return this.isLoadingCartData();
    },
  },
};
</script>

