<template>
  <li class="cart__item product" :class="{disabled: removeRequested}">
    <div class="product__pic">
      <img :src="item.product.image.file.url"
           width="120"
           height="120"
           :alt="item.product.title">
    </div>
    <h3 class="product__title">
      {{ item.product.title }}
    </h3>
    <span class="product__code">
                Артикул: {{ item.product.id }}
              </span>

    <div class="product__counter form__counter">
      <button type="button" aria-label="Убрать один товар" @click.prevent="decrementAmount">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-minus"></use>
        </svg>
      </button>

      <!-- eslint-disable-next-line -->
      <input type="text" v-model.number="amount">

      <button type="button" aria-label="Добавить один товар" @click.prevent="incrementAmount">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-plus"></use>
        </svg>
      </button>
    </div>

    <b class="product__price">
      {{ (item.quantity * item.price) | numberFormat }} ₽
    </b>

    <button class="product__del button-del"
            type="button"
            aria-label="Удалить товар из корзины"
            @click="deleteProductFromCart">
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';

export default {
  name: 'CartItem',
  props: ['item'],
  filters: { numberFormat },
  data() {
    return {
      removeRequested: false,
      removeSuccess: false,
      removeFailed: false,
    };
  },
  computed: {
    amount: {
      get() {
        return this.item.quantity;
      },
      set(value) {
        this.$store.dispatch(
          'updateCartProductAmount',
          { productId: this.item.product.id, amount: value },
        );
      },
    },
  },
  methods: {
    incrementAmount() {
      this.amount += 1;
    },
    decrementAmount() {
      if (this.amount > 1) {
        this.amount -= 1;
      }
    },
    deleteProductFromCart() {
      this.removeRequested = true;
      this.removeSuccess = false;
      this.removeFailed = false;

      this.$store.dispatch(
        'removeCartProduct',
        this.item.product.id,
      )
        .then(() => {
          this.removeRequested = false;
          this.removeSuccess = true;
          this.removeFailed = false;
        })
        .catch(() => {
          this.removeRequested = false;
          this.removeSuccess = false;
          this.removeFailed = true;
        });
    },
  },
};
</script>

<style scoped>
  .product__counter button {
    cursor: pointer;
  }

  li.cart__item {
    position: relative;
  }

  .product__del {
    cursor: pointer;
  }

  .disabled::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 9999;
    background-color: rgba(0,0,0, .3);
  }
</style>
