            глубину не более 1&nbsp;метра.
          </p>
        </div>
      </div>
    </section>
    </div>
  </main>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import axios from 'axios';
import { CONFIG } from '@/config';
import { mapActions } from 'vuex';

export default {
  name: 'ProductPage',
  data() {
    return {
      productAmount: 1,
      productData: null,
      productLoading: false,
      productLoadingError: false,
      productAdded: false,
      productAddSending: false,
      productAddFailed: false,
    };
  },
  computed: {
    product() {
      return this.productData ?? null;
    },
    category() {
      return this.product ? this.product.category : null;
    },
  },
  filters: {
    numberFormat,
  },
  methods: {
    ...mapActions(['addProductToCard']),
    addToCart() {
      this.productAdded = false;
      this.productAddSending = true;
      this.productAddFailed = false;
      this.addProductToCard({ productId: this.product.id, amount: this.productAmount })
        .then(() => {
          this.productAdded = true;
          this.productAddSending = false;
          this.productAddFailed = false;

          setTimeout(() => {
            this.productAdded = false;
          }, 3000);
        }).catch(() => {
          this.productAdded = false;
          this.productAddSending = false;
          this.productAddFailed = true;

          setTimeout(() => {
            this.productAddFailed = false;
          }, 3000);
        });
    },
    incrementProductAmount() {
      this.productAmount += 1;
    },
    decrementProductAmount() {
      if (this.productAmount > 1) {
        this.productAmount -= 1;
      }
    },
    loadProduct() {
      this.productLoading = true;
      this.productLoadingError = false;

      const url = (CONFIG.api.base_url)
        + (CONFIG.api.endpoints.product).replace(':id', this.$route.params.productId);
      axios.get(url)
        .then((response) => {
          this.productData = response.data;
          this.productLoading = false;
        })
        .catch(() => {
          this.productLoadingError = true;
          this.productLoading = false;
        });
    },
  },
  watch: {
    '$route.params.productId': {
      handler() {
        this.loadProduct();
      },
      immediate: true,
    },
  },
};
</script>

<style scoped>
  .form__counter button {
    cursor: pointer;
  }
  .loader, .error-loading {
    text-align: center;
  }
</style>
