<template>
  <aside class="filter">
    <h2 class="filter__title">Фильтры</h2>

    <form class="filter__form form" action="#" method="get" @submit.prevent="submit">
      <fieldset class="form__block">
        <legend class="form__legend">Цена</legend>
        <!-- eslint-disable-next-line -->
        <label class="form__label form__label--price">
          <input class="form__input" type="text" name="min-price" v-model.number="filterPriceFrom">
          <span class="form__value">От</span>
        </label>
        <!-- eslint-disable-next-line -->
        <label class="form__label form__label--price">
          <input class="form__input" type="text" name="max-price" v-model.number="filterPriceTo">
          <span class="form__value">До</span>
        </label>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Категория</legend>
        <!-- eslint-disable-next-line -->
        <label class="form__label form__label--select">
          <select class="form__select"
                  type="text"
                  name="category"
                  v-model.number="filterCategoryId">
            <option value="0">Все категории</option>
            <option :value="category.id"
                    v-for="category in categories"
                    :key="category.id"
            >{{ category.title }}</option>
          </select>
        </label>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Цвет</legend>
        <ul class="colors">
          <li class="colors__item" v-for="color in productColors" :key="color.id">
            <!-- eslint-disable-next-line -->
            <label class="colors__label">
              <input class="colors__radio sr-only"
                     type="radio"
                     name="color"
                     :value="color.id"
                     v-model="filterColor">
              <span class="colors__value" :style="`background-color: ${color.code};`"></span>
            </label>
          </li>
        </ul>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Объемб в ГБ</legend>
        <ul class="check-list">
          <li class="check-list__item">
            <!-- eslint-disable-next-line -->
            <label class="check-list__label">
              <input class="check-list__check sr-only"
                     type="checkbox"
                     name="volume"
                     value="8"
                     checked="">
              <span class="check-list__desc">
                    8
                    <span>(313)</span>
                  </span>
            </label>
          </li>
          <li class="check-list__item">
            <!-- eslint-disable-next-line -->
            <label class="check-list__label">
              <input class="check-list__check sr-only" type="checkbox" name="volume" value="16">
              <span class="check-list__desc">
                    16
                    <span>(461)</span>
                  </span>
            </label>
          </li>
          <li class="check-list__item">
            <!-- eslint-disable-next-line -->
            <label class="check-list__label">
              <input class="check-list__check sr-only" type="checkbox" name="volume" value="32">
              <span class="check-list__desc">
                    32
                    <span>(313)</span>
                  </span>
            </label>
          </li>
          <li class="check-list__item">
            <!-- eslint-disable-next-line -->
            <label class="check-list__label">
              <input class="check-list__check sr-only" type="checkbox" name="volume" value="64">
              <span class="check-list__desc">
                    64
                    <span>(313)</span>
                  </span>
            </label>
          </li>
          <li class="check-list__item">
            <!-- eslint-disable-next-line -->
            <label class="check-list__label">
              <input class="check-list__check sr-only"
                     type="checkbox"
                     name="volume"
                     value="128">
              <span class="check-list__desc">
                    128
                    <span>(313)</span>
                  </span>
            </label>
          </li>
          <li class="check-list__item">
            <!-- eslint-disable-next-line -->
            <label class="check-list__label">
              <input class="check-list__check sr-only"
                     type="checkbox"
                     name="volume"
                     value="264">
              <span class="check-list__desc">
                    264
                    <span>(313)</span>
                  </span>
            </label>
          </li>
        </ul>
      </fieldset>

      <button class="filter__submit button button--primery" type="submit">
        Применить
      </button>
      <button class="filter__reset button button--second" type="button" @click="reset">
        Сбросить
      </button>
    </form>
  </aside>
</template>

<script>
import axios from 'axios';
import { CONFIG } from '@/config';

export default {
  name: 'ProductFilter',
  props: ['priceFrom', 'priceTo', 'categoryId', 'color'],
  data() {
    return {
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: 0,
      filterColor: 0,
      categoriesData: null,
      productColorsData: null,
    };
  },
  computed: {
    categories() {
      return this.categoriesData ? this.categoriesData.items : [];
    },
    productColors() {
      return this.productColorsData ? this.productColorsData.items : [];
    },
  },
  watch: {
    priceFrom(value) {
      this.filterPriceFrom = value;
    },
    priceTo(value) {
      this.filterPriceTo = value;
    },
    categoryId(value) {
      this.filterCategoryId = value;
    },
    color(value) {
      this.filterColor = value;
    },
  },
  methods: {
    submit() {
      this.$emit('update:priceFrom', this.filterPriceFrom);
      this.$emit('update:priceTo', this.filterPriceTo);
      this.$emit('update:categoryId', this.filterCategoryId);
      this.$emit('update:color', this.filterColor);
      this.$emit('myEvent');
    },
    reset() {
      this.$emit('update:priceFrom', 0);
      this.$emit('update:priceTo', 0);
      this.$emit('update:categoryId', 0);
      this.$emit('update:color', '');
      this.$emit('myEvent');
    },
    loadCategories() {
      axios.get((CONFIG.api.base_url) + (CONFIG.api.endpoints.productCategories))
        // eslint-disable-next-line no-return-assign
        .then((response) => this.categoriesData = response.data);
    },
    loadProductColors() {
      axios.get((CONFIG.api.base_url) + (CONFIG.api.endpoints.productColors))
        // eslint-disable-next-line no-return-assign
        .then((response) => this.productColorsData = response.data);
    },
  },
  created() {
    this.loadCategories();
    this.loadProductColors();
  },
};
</script>

