<template>
  <main class="content container">

    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        Каталог
      </h1>
      <span class="content__info">
        {{ countProducts }} товара
      </span>
    </div>

    <div class="content__catalog">
      <ProductFilter
        :price-from.sync="filterPriceFrom"
        :price-to.sync="filterPriceTo"
        :category-id.sync="filterCategoryId"
        :color.sync="filterColor"
      />

      <div v-if="productLoading" class="loader">
        <img src="/img/Iphone-spinner-2.gif" alt="Loading...">
      </div>

      <h3 class="error-loading" v-if="productsLoadingError">
        Упс... что-то пошло не так. Мы уже вкурсе но нам все равно XDDD.<br>
        <button @click="loadProducts">Попробовать еще раз</button>
      </h3>

      <section class="catalog">
        <ProductList :products="products"/>
        <BasePagination :page.sync="page" :total-count="countProducts" :per-page="productPerPage" />
      </section>

    </div>

  </main>
</template>

<script>
import ProductFilter from '@/components/ProductFilter.vue';
import BasePagination from '@/components/BasePagination.vue';
import ProductList from '@/components/ProductList.vue';
import axios from 'axios';
import { CONFIG } from '@/config';

export default {
  name: 'MainPage',
  components: { ProductFilter, BasePagination, ProductList },
  data() {
    return {
      page: 1,
      productPerPage: 6,
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: 0,
      filterColor: 0,
      productsData: null,
      productLoading: true,
      productsLoadingError: false,
    };
  },
  computed: {
    products() {
      return this.productsData
        ? this.productsData.items.map((product) => ({
          ...product,
          img: product.image.file.url,
        }))
        : [];
    },
    countProducts() {
      return this.productsData ? this.productsData.pagination.total : 0;
    },
  },
  methods: {
    loadProducts() {
      this.productLoading = true;
      this.productsLoadingError = false;
      clearTimeout(this.loadProductsTimer);
      this.loadProductsTimer = setTimeout(() => {
        const params = {
          page: this.page,
          limit: this.productPerPage,
        };

        if (this.filterCategoryId > 0) {
          params.categoryId = this.filterCategoryId;
        }

        if (this.filterPriceFrom > 0) {
          params.minPrice = this.filterPriceFrom;
        }

        if (this.filterPriceTo > 0) {
          params.maxPrice = this.filterPriceTo;
        }

        if (this.filterColor > 0) {
          params.colorId = this.filterColor;
        }

        axios.get(
          (CONFIG.api.base_url) + (CONFIG.api.endpoints.productList),
          { params },
        )
          // eslint-disable-next-line no-return-assign
          .then((response) => {
            this.productsData = response.data;
            this.productLoading = false;
          }).catch(() => {
            this.productLoading = false;
            this.productsLoadingError = true;
          });
      }, 0);
    },
  },
  watch: {
    page() {
      this.loadProducts();
    },
    filterCategoryId() {
      this.loadProducts();
    },
    filterPriceFrom() {
      this.loadProducts();
    },
    filterPriceTo() {
      this.loadProducts();
    },
    filterColor() {
      this.loadProducts();
    },
  },
  created() {
    this.loadProducts();
  },
};
</script>

<style scoped>
.loader, .error-loading {
  text-align: center;
}
</style>
