<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html">
            Каталог
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="cart.html">
            Корзина
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        3 товара
      </span>
    </div>
    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
        <div class="cart__field">
          <div class="cart__data">
            <BaseFormText title="ФИО"
                          :error="formError.name"
                          v-model="formData.name"
                          placeholder="Введите ваше полное имя" />

            <BaseFormText title="Адрес доставки"
                          :error="formError.address"
                          v-model="formData.address"
                          placeholder="Введите ваш адрес"/>

            <BaseFormText title="Телефон"
                          :error="formError.phone"
                          v-model="formData.phone"
                          input-type="tel"
                          placeholder="Введите ваш телефон"/>

            <BaseFormText title="Email"
                          :error="formError.email"
                          v-model="formData.email"
                          input-type="email"
                          placeholder="Введи ваш Email" />

            <BaseFormTextarea title="Комментарий к заказу"
                              :error="formError.comment"
                              v-model="formData.comment"
                              placeholder="Ваши пожелания"/>
          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <!-- eslint-disable-next-line -->
                <label class="options__label">
                  <input class="options__radio sr-only"
                         type="radio"
                         name="delivery"
                         value="0"
                         checked="">
                  <span class="options__value">
                    Самовывоз <b>бесплатно</b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <!-- eslint-disable-next-line -->
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="500">
                  <span class="options__value">
                    Курьером <b>500 ₽</b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <!-- eslint-disable-next-line -->
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="card">
                  <span class="options__value">
                    Картой при получении
                  </span>
                </label>
              </li>
              <li class="options__item">
                <!-- eslint-disable-next-line -->
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="cash">
                  <span class="options__value">
                    Наличными при получении
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <OrderCartItem v-for="item in productsInCart" :item="item" :key="item.product.id"/>
          </ul>

          <div class="cart__total">
            <p>Доставка: <b>500 ₽</b></p>
            <p>
              Итого: <b>{{ totalProducts }}</b> товара
              на сумму <b>{{ cartTotalPrice }} ₽</b></p>
          </div>

          <button class="cart__button button button--primery"
                  type="submit" :disabled="sendOrder">
            Оформить заказ
          </button>
        </div>
        <div class="loader" v-if="sendOrder">
          <img src="/img/Iphone-spinner-2.gif" alt="Загрузка...">
        </div>
        <div class="cart__error form__error-block" v-if="sendOrderFail">
          <h4>Заявка не отправлена!</h4>
          <p>
            {{ formErrorMessage }}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import BaseFormText from '@/components/BaseFormText.vue';
import BaseFormTextarea from '@/components/BaseFormTextarea.vue';
import OrderCartItem from '@/components/OrderCartItem.vue';
import numberFormat from '@/helpers/numberFormat';
import { mapGetters } from 'vuex';
import axios from 'axios';
import { CONFIG } from '@/config';

export default {
  name: 'OrderPage',
  components: { BaseFormTextarea, BaseFormText, OrderCartItem },
  data() {
    return {
      formData: {},
      formError: {},
      sendOrder: false,
      sendOrderFail: false,
      formErrorMessage: '',
    };
  },
  computed: {
    ...mapGetters({
      productsInCart: 'cartDetailProducts',
      cartTotalPrice: 'cartTotalPrice',
    }),
    totalProducts() {
      return this.productsInCart ? this.productsInCart.length : 0;
    },
  },
  filters: { numberFormat },
  methods: {
    order() {
      this.sendOrder = true;
      this.sendOrderFail = false;
      this.formError = {};
      this.formErrorMessage = '';

      const url = (CONFIG.api.base_url) + (CONFIG.api.endpoints.createOrder);
      const params = { userAccessKey: this.$store.state.userAccessKey };
      axios.post(url, this.formData, { params })
        .then((response) => {
          this.sendOrder = false;
          this.sendOrderFail = false;
          this.$store.commit('resetCart');
          this.$store.commit('updateOrderInfo', response.data);
          this.$router.push({ name: 'orderInfo', params: { orderId: response.data.id } });
        })
        .catch((error) => {
          this.sendOrder = false;
          this.sendOrderFail = true;
          this.formError = error.response.data.error.request || {};
          this.formErrorMessage = error.response.data.error.message || '';
        });
    },
  },
};
</script>

<style scoped>
.loader {
  text-align: center;
}
</style>
